@import "./variables.module.scss";

@keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}

.container{
    margin-left: auto;
    margin-right: auto;
}
.loading {
    width: 100px;
    height: 100px;
    border: 10px solid #f3f3f3;
    border-top: 10px solid #ecd825;
    border-radius: 50%;
    animation: spinner 1.5s linear infinite;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (max-width: $mobile-medium-bp){
    .loading{
        width: 50px;
        height: 50px;
    }
}